<template>
  <div class="mt-4">
      <v-card-title>
      <div class="text-h5 font-weight-light">
        Data Employee
      </div>
      <v-spacer></v-spacer>
      <v-btn
        color="primary"
        depressed
        elevation="2"
        @click="employee.dialog = true"
      >
      <v-icon>mdi-upload</v-icon>
      UPLOAD Data Employee dan Iuran</v-btn>
    </v-card-title>
    <v-card class="pa-2 ma-2">
      <v-card-title>
        <v-text-field
          v-model="search"
          append-icon="mdi-magnify"
          label="Search"
          small
          hide-details
          width="100px"
        ></v-text-field>
      </v-card-title>
      <v-data-table
        :headers="headers"
        :items="employee.employees"
        loading-text="Loading... Please wait"
        :loading="employee.loading"
        :search="search"
        background-color="primary"
        class="elevation-1"
      >
        <template #[`item.index`]="{ item }">
          {{ employee.employees.indexOf(item)+1 }}.
        </template>

        <template #[`item.action`]="{ item }">
          <v-btn depressed
            elevation="2"
            color="primary"
            @click="aktifkan(item.id)">
            <v-icon
              small
              class="mr-2"
            >
              mdi-check
            </v-icon> Aktifkan
          </v-btn>
        </template>
      </v-data-table>
    </v-card>
    <Upload/>
  </div>
</template>
<script>
import { mapState, mapActions } from 'vuex'
import store from '@/store'
import Upload from '@/components/Employee/Upload.vue'
export default {
  name: 'DataEmployee',
  components: {
    Upload
  },
  data: () => ({
    search: '',
    headers: [
      {
        sortable: false,
        text: '#',
        value: 'index'
      },
      {
        text: 'NIK',
        align: 'start',
        value: 'NIK'
      },
      { text: 'Nama', value: 'name' },
      { text: 'Company', value: 'company' },
      { text: 'Action', value: 'action' }
    ]
  }),
  mounted: function () {
    store.dispatch('employee/reqEmployee')
  },
  computed: {
    ...mapState('employee', {
      employee: (state) => state
    })
  },
  methods: {
    ...mapActions('employee', [
      'aktifkan'
    ])
  }
}
</script>
